import React, { useEffect } from 'react';
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { SET_CREDENTIALS, CLEAR_STEP } from '../../../redux/constants/actionTypes';
import { useRegisterMutation } from "./../../auth/slices/authApiSlice";
import { Helmet } from 'react-helmet'
import { getFixedT } from 'i18next';
import 'bootstrap/dist/css/bootstrap.min.css';

const Success = () => {
    const t = getFixedT(null, null, 'success')
    const navigate = useNavigate();
    const dispatch = useDispatch()
    const step = useSelector((state) => state.step)
    const [register] = useRegisterMutation()
    useEffect(() => {
        register({ first_name: step.firstName, last_name: step.lastName, email: step.email, password: step.password, phone_number: step.phoneNumber }).unwrap().then(function (response) {
            if (response) {
                dispatch({ type: SET_CREDENTIALS, payload: response })
                dispatch({ type: CLEAR_STEP })

            }
            setTimeout(() => {
                navigate('/membre')
            }, 4000);
        }).catch((rejected) => console.error(rejected));
    }, [])


    return (
        <>
            <Helmet title={t('title')} />
            <div class="container">
                <div class="row align-items-center mt-4">
                    <div class="col-md-12">
                        <h3>{t('Your payment was processed successfully! You will be directed to dashboard after 3 seconds')}</h3>
                    </div>
                </div>
            </div>

        </>
    )
}

export default Success;
